import "../../../App.css";
import "../../style.css";
import { React, useState } from "react";
import headStyle from "../../styling/HeaderStyle";
import AppBar from "@material-ui/core/AppBar";
import Slide from "@material-ui/core/Slide";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import { useHistory, useLocation } from "react-router-dom";
import Drawe from "./Drawer";
import { useTheme } from "@material-ui/core/styles";
import { HeaderHome } from "./HeaderHome";
import { HeaderLogged } from "./HeaderLogged";
import { MuiThemeProvider, createTheme } from "@material-ui/core/styles";

const options = { disableHysteresis: true, threshold: 0 };
function HideOnScroll(props) {
  const { children } = props;
  const trigger = useScrollTrigger({ threshold: 0 });
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}
const Cappbar = (props) => {
  const classes = headStyle();
  const res = useScrollTrigger(options);
  let location = useLocation();

  const { children } = props;

  return res !== true &&( location.pathname === "/" || location.pathname === "/signin") ? (
    <AppBar {...props} color="transparent" elevation={0}>
      {children}
    </AppBar>
  ) : props.token ? (
    <AppBar {...props} color="transparent" className={classes.dashheader}>
      {children}
    </AppBar>
  ) : (
    <HideOnScroll {...props}>
      <AppBar
        {...props}
        color="default"
        style={{ borderRadius: "0 0 1rem 1rem" }}
      >
        {children}
      </AppBar>
    </HideOnScroll>
  );
};

function Header(props) {
  const history = useHistory();
  let location = useLocation();
  // const theme = useTheme();
  const [open, setOpen] = useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };
  const theme = createTheme({});
  const classes = headStyle();

  return (
    <div className={classes.grow}>
      <Cappbar position="fixed" {...props} open={props.open}>
        {props.token ? (
          <MuiThemeProvider theme={theme}>
          <HeaderLogged
            handleDrawerOpen={handleDrawerOpen}
            open={props.open}
            {...props}
          />
          </MuiThemeProvider>
        ) : (
          <HeaderHome handleDrawerOpen={handleDrawerOpen} open={props.open} />
        )}
      </Cappbar>
      <Drawe
        history={history}
        classes={classes}
        location={location}
        handleDrawerClose={handleDrawerClose}
        theme={theme}
        open={open}
      />
    </div>
  );
}

export default Header;
