import "./App.css";
import AppBody from "./Content/AppBody.js";
import {
  MuiThemeProvider,
  createTheme,
  makeStyles,
} from "@material-ui/core/styles";
import { useEffect, useState } from "react";
import Loading from "./Content/component/Loading";

const theme = createTheme({
  palette: {
    primary: {
      main: "#343f56",
      contrastText: "#fff",
    },
    secondary: {
      main: "#dc004e",
      contrastText: "#343f56",
    },
  },
});

function App() {
  const [opacity, setOpacity] = useState(0);
  const [display, setDisplay] = useState("block");
  useEffect(() => {
    window.addEventListener("load", (event) => {
      const image = document.querySelector("img");
      if (image) {
        const isLoaded = image?.complete && image?.naturalHeight !== 0;
        if (isLoaded) {
          setDisplay("none");
          setOpacity(1);
        }
      }
      setDisplay("none");
      setOpacity(1);
    });
  }, []);
  return (
    <>
      <Loading display={display} />
      <div className="bg" style={{ opacity: opacity }}>
        <MuiThemeProvider theme={theme}>
          <AppBody />
        </MuiThemeProvider>
      </div>
    </>
  );
}

export default App;
