import Charts, { Piechart } from "./Chart";
import Deposits from "./Deposits";
import Orders from "./Orders";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import clsx from "clsx";
import { useStyles } from "../../styling/dashbordstyle";
import { useEffect } from "react";
import { Box, Typography } from "@material-ui/core";

const griditems = [
  {
    title: "Recomanded",
  },
  {
    title: "Strongly recomanded",
  },
  {
    title: "Not recemanded",
  },
  {
    title: "Startup Total",
  },
];

export const DashOverView = ({ startup, data, dashloading }) => {
  // console.log(startup);
  const role = data.role;

  const griditems =
    role === "admin"
      ? [
          {
            title: "Strongly recomanded",
            style: {
              backgroundColor: "rgba(200, 250, 205,0.22)",
              alignItems: "center",
              color: "rgb(31 98 38)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
          {
            title: "Recomanded",
            style: {
              backgroundColor: "rgba(208, 242, 255, 0.22)",
              alignItems: "center",
              color: "rgb(37 97 121)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
          {
            title: "Not recemanded",
            style: {
              backgroundColor: "rgba(255, 231, 217, 0.22)",
              alignItems: "center",
              color: "rgb(135 77 43)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
          {
            title: "Startup Total",
            style: {
              backgroundColor: "rgba(255, 247, 205, 0.22)",
              alignItems: "center",
              color: "rgb(113 100 36)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
        ]
      : [
          {
            title: "onProgress",
            style: {
              backgroundColor: "rgba(255, 231, 217, 0.22)",
              alignItems: "center",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
              color: "rgb(135 77 43)",
            },
          },
          {
            title: "Evaluated",
            style: {
              backgroundColor: "rgba(200, 250, 205, 0.22)",
              alignItems: "center",
              color: "rgb(31 98 38)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
          {},
          {
            title: "Tasks Total",
            style: {
              backgroundColor: "rgba(255, 247, 205, 0.22)",
              alignItems: "center",
              color: "rgb(113 100 36)",
              boxShadow:
              "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
            },
          },
        ];
  const classes = useStyles();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  return (
    <Grid container spacing={3} alignItems="center" justifyContent="center">
      {griditems.map((elem, index) =>
        elem.title ? (
          <Grid item xs={12} lg={3} key={index}>
            <Paper
              className={fixedHeightPaper}
              style={elem.style}
              // elevation={9}
            >
              <Deposits
                startup={startup}
                griditem={elem}
                styling={elem.style}
                dashloading={dashloading}
              />
            </Paper>
          </Grid>
        ) : (
          <Grid item xs={12}  lg={3} key={index}></Grid>
        )
      )}
      <Grid item xs={12} lg={4}>
        <Paper
          className={classes.paper}
          style={{ overflow: "hidden" ,display: "block" }}
        >
          {/* <Typography className={classes.titlehead}>Startups Chart</Typography> */}
          <Piechart startup={startup} role={role} />
        </Paper>
      </Grid>
      <Grid item xs={12} lg={8}>
        <Paper className={classes.paper} style={{display: "block"}}>
          <Charts startup={startup} role={role} />
        </Paper>
      </Grid>
      <Grid item xs={12}>
        <Paper className={classes.paper} style={{display: "block"}} >
          <Orders startup={startup} role={data.role} />
        </Paper>
      </Grid>
    </Grid>
  );
};
