import { React, useState, useEffect } from "react";
import clsx from "clsx";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import Backdrop from "@material-ui/core/Backdrop";
import { MainListItems } from "./listItems";
import Logo from "../../imgs/llogodash.svg";
import fetchips from "../../fetchingData/fatchdata";
import { useStyles } from "../../styling/dashbordstyle";
import { InnerBoard } from "./InnerBoard.js";
import Box from "@material-ui/core/Box";
import Copyright from "../copyright";
import { dbip } from "../../fetchingData/fatchdata";
import { useHistory, useLocation } from "react-router";
import { PeopleSharp } from "@material-ui/icons";
import { Typography } from "@material-ui/core";
import { MuiThemeProvider, createTheme } from "@material-ui/core/styles";
export default function Dashboard({
  setStartup,
  startup,
  setStartups,
  startups,
  token,
  open,
  data,
  setOpen,
}) {
  const theme = createTheme({});
  const classes = useStyles();
  const location = useLocation();
  const [dashloading, setdashloading] = useState(true);
  let history = useHistory();
  useEffect(() => {
    // history.replace(location.pathname.toLowerCase());
    const Getstartups = async () => {
      const data = await fetch(`${dbip}/api/v1/correction`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });

      const res = await data.json();
          console.log(res.data)
      if (res.success) setStartup(res.data);
      else {
        if (res.error === "Not authorize to access this route...")
          localStorage.clear();
      }
      setdashloading(false);
    };
    Getstartups();
    // console.log(startup)
  }, [setStartup, setdashloading]);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <MuiThemeProvider theme={theme}>
        <Drawer
          variant="permanent"
          classes={{
            paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
          }}
          open={open}
        >
          <div className={classes.toolbarIcon}>
            <IconButton size="medium" onClick={handleDrawerOpen}>
              <img src={Logo} className={classes.logo} alt="img" />
            </IconButton>
            <IconButton
              onClick={handleDrawerClose}
              style={{ display: !open ? "none" : "block" }}
            >
              <ChevronLeftIcon color="primary" />
            </IconButton>
          </div>
          {/* <Divider /> */}
          <List disablePadding>
            <MainListItems
              handleDrawerClose={handleDrawerClose}
              role={data.role}
            />
          </List>
        </Drawer>
      </MuiThemeProvider>
      <Backdrop
        className={classes.backdrop}
        open={open}
        onClick={handleDrawerClose}
      />
      <main className={classes.contentOpen} onClick={handleDrawerClose}>
        <div className={classes.appBarSpacer} />
        <InnerBoard
          startup={startup}
          startups={startups}
          setStartup={setStartup}
          setStartups={setStartups}
          token={token}
          data={data}
          dashloading={dashloading}
        />
        <div className={classes.appBarSpacer} />
        <Box pt={4} className={classes.copy}>
          <Copyright />
        </Box>
      </main>
    </div>
  );
}
