import { Typography, Box } from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import { withStyles, makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  cont: {
    display: "flex",
    justifyContent: "space-around",
    paddingLeft: "4px", paddingRight: "4px"
  },
  prog: {
    borderRadius: 5,
    height: 10,
    width : "18%",
    backgroundColor: theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
  },
  succsprog: {
    borderRadius: 5,
    height: 10,
    width : "18%",
    backgroundColor: theme.palette.success.main,
  },
}));

const MentorReviewprogress = ({ progvalue }) => {
  const BorderLinearProgress = withStyles((theme) => ({
    root: {
      height: 10,
      borderRadius: 5,
    },
    colorPrimary: {
      backgroundColor:
        theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
    },
    bar: {
      borderRadius: 5,
      backgroundColor:
        progvalue === 100
          ? theme.palette.success.light
          : theme.palette.error.main,
    },
  }))(LinearProgress);

  return (
    <div style={{ paddingLeft: "4px", paddingRight: "4px" }}>
      <Typography variant="caption" color="primary">
        Review Progress
      </Typography>
      <Box display="flex" alignItems="center">
        <Box width="100%" mr={1}>
          <BorderLinearProgress variant="determinate" value={progvalue} />
        </Box>
        <Box minWidth={35}>
          <Typography variant="body2" color="textSecondary">
            {progvalue}%
          </Typography>
        </Box>
      </Box>
    </div>
  );
};

const getfinalGrade = (fgrades) => {
  let res = [];
  for(const prop in fgrades)
  {
    res.push(fgrades[prop].finalGrade);
  }
  return(res);
}


const AdminReviwProgress = ({fgrades}) => {
  const res = getfinalGrade(fgrades);
  const classes = useStyles();
  return (
    <>
      <Typography variant="caption" color="primary" style={{ paddingLeft: "9px" , paddingBottom:"4px" }}>
        Mentors Progress :
      </Typography>
    <div className={classes.cont}>
      {res.map((elem, index) => (
       elem ? (<div className={classes.succsprog} key={index} />) : (<div className={classes.prog} key={index} />)
      ))}
    </div>
    </>
  );
};

export default function Innercard(props) {
  const role = props.data.role;
  const pvalue =
    props.startup.mentor.total === -1
      ? 0
      : props.startup.mentor.total;
  const progvalue = Math.floor((pvalue / 24) * 100);
  const fgrades = props.startup.mentor
  return (
    <>
      <div style={{ paddingLeft: "9px" }}>
        <Typography variant="caption" color="primary">
          Startups development stage :
        </Typography>
        <Typography variant="caption" color="error" paragraph>
          {props.startup.sds}
        </Typography>
      </div>
      {role === "admin" ? ( <AdminReviwProgress fgrades={fgrades}/>) : (<MentorReviewprogress progvalue={progvalue} />)}
    </>
  );
}
