import React from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import CircularProgress from '@material-ui/core/CircularProgress';

function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
  depositContext: {
    // fontWeight: "600",
    // opacity: 0.7,
    fontFamily: "Montserrat, sans-serif",
    textTransform: "uppercase",
    // color: theme.palette.primary.main,
    // fontSize: "5rem"
  },
  formhead: {
    fontWeight: "600",
    opacity: 0.7,
    fontFamily: "Montserrat, sans-serif",
    textTransform: "uppercase",
    paddingBottom: theme.spacing(3),
    // color: theme.palette.primary.main,
    fontSize: "90%",
    // paddingLeft: theme.spacing(1),
  },
  length: {
    fontWeight: "600",
    // opacity: 0.7,
    fontFamily: "Montserrat, sans-serif",
    textTransform: "uppercase",
    paddingBottom: theme.spacing(3),
    // color: theme.palette.grey[300],
    fontSize: "5rem",
    // paddingLeft: theme.spacing(1),
  },
}));

export default function Deposits({ startup, griditem, styling, dashloading }) {
  // console.log(styling.color);
  const classes = useStyles();
  return (
    <React.Fragment>
      <Typography
        component="h2"
        variant="h6"
        color="primary"
        className={classes.formhead}
        style={{ color: styling.color }}
        gutterBottom
      >
        {griditem.title}
      </Typography>
      <Typography
        component="p"
        variant="h4"
        className={classes.length}
        style={{ color: styling.color }}
      >
        {dashloading ? <CircularProgress /> : startup?.length}
      </Typography>
      <Typography color="textSecondary" className={classes.depositContext}>
        {/* on 1 July, 2021 */}
      </Typography>
      <div>
        <Link color="primary" to="/account/startups">
          <Typography
            color="textSecondary"
            className={classes.depositContext}
            style={{ color: styling.color }}
          >
            View Startups
          </Typography>
        </Link>
      </div>
    </React.Fragment>
  );
}
