import React, { useState } from "react";
import {
  ListItem,
  ListItemIcon,
  ListItemText,
  List,
  Collapse,
  makeStyles,
} from "@material-ui/core";
// import ListItemIcon from "@material-ui/core/ListItemIcon";
// import ListItemText from "@material-ui/core/ListItemText";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import AddCircleOutlineIcon from "@material-ui/icons/AddCircleOutline";
import DashboardIcon from "@material-ui/icons/Dashboard";
import StarsIcon from "@material-ui/icons/Stars";
import { Link, useLocation } from "react-router-dom";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
const useStyles = makeStyles((theme) => ({
  primary: {
    fontFamily: "Public Sans, sans-serif",
    fontWeight: "bold",
    color: theme.palette.primary.main,
    fontSize: "0.9rem",
  },
  primarysub: {
    fontFamily: "Public Sans, sans-serif",
    color: theme.palette.primary.main,
    fontSize: "0.9rem",
  },

  listItem: {
    "&:hover": {
      borderRight: `4px ${theme.palette.primary.main} solid`,
    },
  },
  listItemactive: {
    borderRight: `4px ${theme.palette.primary.main} solid`,
    backgroundColor: "#3f51b52e",
  },
}));

export const MainListItems = ({ handleDrawerClose, role }) => {
  const [open, setOpen] = useState(true);
  const location = useLocation();
  const classes = useStyles();
  return (
    <div>
      <Link to="/account">
        <ListItem
          button
          onClick={handleDrawerClose}
          className={
            location.pathname.toLowerCase() === "/account" 
              ? classes.listItemactive
              : classes.listItem
          }
        >
          <ListItemIcon>
            <DashboardIcon color="primary" />
          </ListItemIcon>
          <ListItemText
            primary="Dashboard"
            classes={{ primary: classes.primary }}
          />
        </ListItem>
      </Link>
      {role === "admin" ? (
        <>
          <ListItem
            button
            className={
             location.pathname.toLowerCase() === "/account/addmember" ||
             location.pathname.toLowerCase() === "/account/deletemember"
                ? classes.listItemactive
                : classes.listItem
            }
            onClick={() => {
              setOpen(!open);
            }}
          >
            <ListItemIcon>
              <PersonAddIcon color="primary" />
            </ListItemIcon>
            <ListItemText
              primary="Members"
              secondary="Settings"
              classes={{ primary: classes.primary }}
            />
          </ListItem>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <List
              component="div"
              style={{ backgroundColor: "rgb(247, 249, 252)" }}
              disablePadding
            >
              <Link to="/account/addmember">
                <ListItem
                  button
                  onClick={handleDrawerClose}
                  className={
                   location.pathname.toLowerCase() === "/account/addmember"
                      ? classes.listItemactive
                      : classes.listItem
                  }
                >
                  <ListItemIcon>
                    <AddCircleOutlineIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText
                    primary="Add Member"
                    classes={{ primary: classes.primarysub }}
                  />
                </ListItem>
              </Link>
              <Link to="/account/deletemember" onClick={handleDrawerClose}>
                <ListItem
                  button
                  className={
                   location.pathname.toLowerCase() === "/account/deletemember"
                      ? classes.listItemactive
                      : classes.listItem
                  }
                >
                  <ListItemIcon>
                    <DeleteOutlineIcon color="primary" />
                  </ListItemIcon>
                  <ListItemText
                    primary="Delete Member"
                    classes={{ primary: classes.primarysub }}
                  />
                </ListItem>
              </Link>
            </List>
          </Collapse>
        </>
      ) : (
        false
      )}

      <Link to="/account/startups">
        <ListItem
          button
          onClick={handleDrawerClose}
          className={
           location.pathname.toLowerCase() === "/account/startups" ||  location.pathname.toLowerCase() === "/account/review" ||  location.pathname.toLowerCase() === "/account/correction"
              ? classes.listItemactive
              : classes.listItem
          }
        >
          <ListItemIcon>
            <StarsIcon color="primary" />
          </ListItemIcon>
          <ListItemText
            primary="Startups"
            classes={{ primary: classes.primary }}
          />
        </ListItem>
      </Link>
    </div>
  );
};
