import React from "react";
import { Link } from "react-router-dom";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { Typography, Button } from "@material-ui/core";
import Chip from "@material-ui/core/Chip";
import HourglassFullRoundedIcon from "@material-ui/icons/HourglassFullRounded";
import DoneOutlineRoundedIcon from "@material-ui/icons/DoneOutlineRounded";
import {
  MuiThemeProvider,
  createTheme,
  makeStyles,
} from "@material-ui/core/styles";

const theme = createTheme({
  palette: {
    primary: {
      main: "#4caf50",
      contrastText: "#fff",
    },
    secondary: {
      main: "#f44336",
      contrastText: "#343f56",
    },
  },
});

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
  Tcell: {
    fontFamily: "Public Sans, sans-serif",
    fontWeight: "bold",
  },
  Trow: {
    fontFamily: "Public Sans, sans-serif",
    color: "#3f51b5",
  },
  but: {
    borderRadius: theme.spacing(2),
    borderColor: "#3f51b5",
  },
  butlab: {
    color: "#3f51b5",
  },
  butroot: {
    color: "#3f51b5 !important",
    "&:hover": {
      backgroundColor: "#3f51b512",
    },
  },
  focusVisible: {
    color: "#3f51b5 !important",
  },
}));

export default function Orders({ startup, role }) {
  const done = (
    <Chip
      variant="outlined"
      color="primary"
      label="Reviewed"
      icon={<DoneOutlineRoundedIcon />}
    />
  );
  const inprog = (
    <Chip
      variant="outlined"
      color="secondary"
      label="In progress"
      icon={<HourglassFullRoundedIcon />}
    />
  );

  const classes = useStyles();
  return (
    <React.Fragment>
      <Typography
        component="h2"
        variant="h6"
        gutterBottom
        style={{ alignSelf: "baseline", fontFamily: "Public Sans, sans-serif" }}
      >
        Recent Startups :
      </Typography>
      <MuiThemeProvider theme={theme}>
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell className={classes.Tcell}>Name</TableCell>
              <TableCell className={classes.Tcell}>Devlompent Stage</TableCell>
              <TableCell className={classes.Tcell}>Status</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {startup
              ?.slice(0)
              .reverse()
              .map((row, index) => (
                <TableRow key={index}>
                  <TableCell className={classes.Trow}>{row.Sname}</TableCell>
                  <TableCell className={classes.Trow}>{row.sds}</TableCell>
                  <TableCell className={classes.Trow}>
                    {role === "admin"
                      ? row.evaluated
                        ? done
                        : inprog
                      : row.mentor.finalGrade
                      ? done
                      : inprog}
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </MuiThemeProvider>
      <div className={classes.seeMore}>
        <Link color="primary" to="/account/startups">
          <Button
            classes={{
              root: classes.butroot,
              outlined: classes.but,
              label: classes.butlab,
              focusVisible: classes.focusVisible,
            }}
            variant="outlined"
            style={{
              aslignSelf: "baseline",
              fontFamily: "Public Sans, sans-serif",
            }}
          >
            See more Startups
          </Button>
        </Link>
      </div>
    </React.Fragment>
  );
}
