import { alpha, makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import InputBase from "@material-ui/core/InputBase";
import Badge from "@material-ui/core/Badge";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import SearchIcon from "@material-ui/icons/Search";
import AccountCircle from "@material-ui/icons/AccountCircle";
import MenuRoundedIcon from "@material-ui/icons/MenuRounded";
import NotificationsIcon from "@material-ui/icons/Notifications";
import MoreIcon from "@material-ui/icons/MoreVert";
import { React, useState } from "react";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import { useHistory } from "react-router-dom";
import clsx from "clsx";
import {
  Box,
  Container,
  Paper,
  Divider,
  Button,
  Avatar,
} from "@material-ui/core";
import PersonIcon from "@material-ui/icons/Person";
import SettingsRoundedIcon from "@material-ui/icons/SettingsRounded";
import { MuiThemeProvider, createTheme } from "@material-ui/core/styles";
import { dbip } from "../../fetchingData/fatchdata";
import defaultlogo from "../../imgs/user.svg";
export const HeaderLogged = (props) => {
  const drawerWidth = 280;

  const theme = createTheme({});

  const useStyles = makeStyles((theme) => ({
    grow: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      display: "none",
      [theme.breakpoints.up("sm")]: {
        display: "block",
      },
    },
    search: {
      flexGrow: 1,
      position: "relative",
      borderRadius: theme.shape.borderRadius,
      backgroundColor: alpha(theme.palette.primary.light, 0.15),
      "&:hover": {
        backgroundColor: alpha(theme.palette.primary.light, 0.25),
      },
      marginRight: theme.spacing(2),
      marginLeft: 0,
      width: "100%",
      [theme.breakpoints.up("sm")]: {
        marginLeft: theme.spacing(3),
        width: "auto",
      },
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: "100%",
      position: "absolute",
      pointerEvents: "none",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
    inputRoot: {
      color: "inherit",
      width: "100%",
    },
    inputInput: {
      flexGrow: 1,
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create("width"),
      width: "100%",
      [theme.breakpoints.up("md")]: {
        width: "20ch",
      },
    },
    sectionDesktop: {
      display: "none",
      [theme.breakpoints.up("md")]: {
        display: "flex",
      },
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("md")]: {
        display: "none",
      },
    },
    shift: {
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.complex,
      }),
      width: "100%",
      marginLeft: 0,
      [theme.breakpoints.up("sm")]: {
        paddingLeft: "72px",
      },
      paddingLeft: "56px",
    },
    shiftopen: {
      transition: theme.transitions.create(["width", "margin"], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      // opacity:"0",
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
      paddingLeft: 0,
    },
    username: {
      fontFamily: "Public Sans, sans-serif",
      fontWeight: "bold",
      color: theme.palette.primary.main,
      fontSize: "0.875rem",
    },
    email: {
      fontFamily: "Public Sans, sans-serif",
      opacity: 0.5,
      color: theme.palette.primary.light,
      fontSize: "0.875rem",
    },
    items: {
      fontFamily: "Public Sans, sans-serif",
      fontSize: "0.875rem",
    },
    toolbar: {
      [theme.breakpoints.up("md")]: {
        height: "92px",
      },
    },
    con: {
      width: "240px",
    },
    head: {
      padding: theme.spacing(2, 2),
    },
    headc:{
      display:"flex",
      justifyContent:"space-between",
      alignItems: "center"
    },
    pp: {
      borderRadius: theme.spacing(2),
      padding: 0,
    },
    divitems: {
      padding: "0 !important",
      width: "100% !important",
    },
    cnintems: {
      marginTop: theme.spacing(2),
    },
  }));
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);
  const history = useHistory();
  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  const user = JSON.parse(localStorage.getItem("user"));

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  const Logout = async () => {
    await handleMenuClose();
    localStorage.removeItem("token");
    localStorage.removeItem("id");
    localStorage.clear();
    window.location.href = "/";
  };
  const ulogo =
    user?.logo === "no-photo.jpg"
      ? defaultlogo
      : `${dbip}/api/v1/startups/photo/` + user?.logo;
  const menuId = "primary-search-account-menu";
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
      id={menuId}
      classes={{
        paper: classes.pp,
        list: classes.divitems,
      }}
      getContentAnchorEl={null}
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <Container disableGutters className={classes.con}>
        <div className={classes.headc}>
          <div className={classes.head} style={{paddingRight:0}}>
            <Typography align="left" className={classes.username}>
              {user?.firstname + " " + user?.lastname}
            </Typography>
            <Typography className={classes.email}>{user?.email}</Typography>
          </div>
          <div className={classes.head} style={{paddingLeft:4}}>
            <Avatar src={ulogo} />
          </div>
        </div>
        <Divider />
        <div className={classes.cnintems}>
          <MenuItem onClick={handleMenuClose}>
            <Box display="flex" justifyContent="center" alignItems="center">
              <PersonIcon style={{ paddingRight: "10px" }} color="primary" />
              <Typography className={classes.items}>Profile</Typography>
            </Box>
          </MenuItem>
          <MenuItem onClick={handleMenuClose} className={classes.sectionMobile}>
            <Box display="flex" justifyContent="center" alignItems="center">
              <NotificationsIcon
                style={{ paddingRight: "10px" }}
                color="primary"
              />
              <Typography className={classes.items}> Notifications</Typography>
            </Box>
          </MenuItem>
          <MenuItem onClick={handleMenuClose}>
            <Box display="flex" justifyContent="center" alignItems="center">
              <SettingsRoundedIcon
                style={{ paddingRight: "10px" }}
                color="primary"
              />
              <Typography className={classes.items}>Settings</Typography>
            </Box>
          </MenuItem>
        </div>
        <Box display="flex" justifyContent="center" className={classes.head}>
          <Button
            onClick={Logout}
            variant="outlined"
            color="primary"
            classes={{
              label: classes.items,
            }}
            fullWidth
            style={{ borderRadius: "16px" }}
          >
            Logout
          </Button>
        </Box>
      </Container>
    </Menu>
  );

  return (
    <MuiThemeProvider theme={theme}>
      <Container
        maxWidth="xl"
        disableGutters={false}
        className={clsx(classes.shift, props.open && classes.shiftopen)}
      >
        <Toolbar className={classes.toolbar}>
          {/* <Typography  className={classes.username} variant="h6" noWrap>
          {props.data?.firstname}
        </Typography> */}
          <div className={classes.search}>
            <div className={classes.searchIcon}>
              <SearchIcon color="primary" />
            </div>
            <InputBase
              placeholder="Search"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ "aria-label": "search" }}
            />
          </div>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            {/* <IconButton aria-label="show 4 new mails" color="inherit">
            <Badge badgeContent={1} color="error">
              <MailIcon  color="primary" />
            </Badge>
          </IconButton> */}
            <IconButton aria-label="show 17 new notifications" color="inherit">
              <Badge badgeContent={1} color="error">
                <NotificationsIcon color="primary" />
              </Badge>
            </IconButton>
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <MenuRoundedIcon color="primary" />
            </IconButton>
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <MenuRoundedIcon color="primary" />
            </IconButton>
          </div>
        </Toolbar>
        {renderMenu}
      </Container>
    </MuiThemeProvider>
  );
};
