import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import CardHeader from "@material-ui/core/CardHeader";
import { useEffect } from "react";
import { useHistory } from "react-router-dom";
import defaultlogo from "../../imgs/user.svg";
import Innercard from "./Innercard";
import { dbip } from "../../fetchingData/fatchdata";
import ReadMoreReact from "read-more-react";
const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    borderRadius: theme.spacing(2),
    height: "100%",
    display: "flex",
    // padding: theme.spacing
    flexDirection: "column",
    boxShadow:
      "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
  },
  cardheaderFinal: {
    backgroundColor: "#4caf5030",
  },
  cardheaderprog: {
    backgroundColor: "#f4433630",
  },
  cardheadertypoF: {
    color: "#357738",
    fontFamily: "Montserrat, sans-serif ",
    fontWeight: "bold",
  },
  cardheadertypoP: {
    color: "#bb2d23",
    fontFamily: "Montserrat, sans-serif ",
    fontWeight: "bold",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  but: {
    color: theme.palette.error.main,
    borderColor: theme.palette.error.main,
  },
  epitch: {
    fontFamily: "Public Sans, sans-serif",
    fontSize: "0.8rem",
    color: theme.palette.primary.main,
    [theme.breakpoints.up("sm")]: {
      fontSize: "1rem",
    },
  },
}));

export const StartupCard = (props) => {
  const history = useHistory();
  const role = props.data.role;
  const isfinished =
    role === "admin"
      ? props.startup.evaluated
      : props.startup.mentor.finalGrade;
  const logo =
    props.startup.logo === "no-photo.jpg"
      ? undefined
      : `${dbip}/api/v1/startups/photo/` + props.startup.logo;
  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardHeader
        classes={
          isfinished
            ? { root: classes.cardheaderFinal, title: classes.cardheadertypoF }
            : { root: classes.cardheaderprog, title: classes.cardheadertypoP }
        }
        title={props.startup.Sname ? props.startup.Sname : "blablablab"}
        titleTypographyProps={{
          variant: "subtitle1",
        }}
      />
      <CardMedia
        className={classes.cardMedia}
        image={logo ? logo : defaultlogo}
        title="Image title"
      />
      <Divider />
      <CardContent className={classes.cardContent}>
        <Typography className={classes.epitch}>
          <ReadMoreReact
            readMoreText={"...click to read more"}
            text={props.startup.epitch ? props.startup.epitch : ""}
          />
        </Typography>
      </CardContent>
      <Innercard {...props} />
      <CardActions>
        {role === "admin" ? (
          <Button
            size="small"
            variant="outlined"
            className={classes.but}
            onClick={() => {
              history.push({
                pathname: "/account/review",
                state: {
                  id: props.startup._id,
                },
              });
            }}
          >
            Review
          </Button>
        ) : (
          <Button
            size="small"
            variant="outlined"
            className={classes.but}
            onClick={() => {
              history.push({
                pathname: "/account/correction",
                state: {
                  id: props.startup._id,
                },
              });
            }}
          >
            Evaluate
          </Button>
        )}
      </CardActions>
    </Card>
  );
};

export const Getstartupcard = (props) => {
  useEffect(() => {
    const Getstartups = async () => {
      const data = await fetch(`${dbip}/api/v1/correction`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
          Authorization: `Bearer ${props.token}`,
        },
      });

      const res = await data.json();
      props.setStartup(res.data);
    };
    Getstartups();
    window.scrollTo(0, 0);
  }, []);
  const data = props.startup ? props.startup : [];
  return data.map((startup, index) => (
    <Grid item key={index} xs={12} sm={6} md={4} lg={3}>
      <StartupCard startup={startup} key={index} data={props.data} />
    </Grid>
  ));
};
