import { Button, Container, Grid, Paper, Typography } from "@material-ui/core";
import { addmentor } from "../../inputdb/addmentor";
import PutInputs from "../Inputs";
import { React, useEffect, useState } from "react";
import { useForm } from "react-hooks-helper";
import Alert from "@material-ui/lab/Alert";
import Slide from "@material-ui/core/Slide";
import { dbip } from "../../fetchingData/fatchdata";
import useStyles from "../../styling/inputstyle";

export const AddMentor = () => {
  const createuser = async () => {
    const token = localStorage.getItem("token");
    const formdata = new FormData();
    formdata.append("file", pictures);
    formdata.append("firstname", FirstName);
    formdata.append("lastname", LastName);
    formdata.append("email", email);
    formdata.append("role", role);
    formdata.append("phone", Phone);
    formdata.append("password", password);
    const res = await fetch(`${dbip}/api/v1/users`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
      },
      body: formdata,
    });
    const data = await res.json();
    if (!data.success) {
      setWAlert({ state: true, msg: data.error });
      setTimeout(() => {
        setWAlert({ state: false, msg: data.error });
      }, 6000);
    } else {
      setSAlert({ state: true, msg: "user Added" });
      setTimeout(() => {
        setSAlert({ state: false, msg: "user Added" });
      }, 6000);
    }
    window.scroll(0,0);
  };

  const defaultValues = {
    FirstName: "",
    LastName: "",
    username: "",
    password: "",
    email: "",
    role: "",
  };

  const [{ FirstName, LastName, password, email, role }, setValue] =
    useForm(defaultValues);
  const [pictures, setPic] = useState({});
  const [prevpic, setPPic] = useState("");
  const [Salert, setSAlert] = useState({ state: false, msg: "" });
  const [Walert, setWAlert] = useState({ state: false, msg: "" });
  const [Phone, setPhone] = useState("");
  const onDrop = (picture, file) => {
    setPic(picture[0]);
    setPPic(file);
  };
  const classes = useStyles();

  return (
    <>
      <Slide direction="down" in={Salert.state} mountOnEnter unmountOnExit >
        <Alert severity="success" variant="filled" style={{marginBottom:"2rem"}}>
          {Salert.msg}
        </Alert>
      </Slide>
      <Slide direction="down" in={Walert.state} mountOnEnter unmountOnExit>
        <Alert severity="warning" variant="filled" style={{marginBottom:"2rem"}}>
          {Walert.msg}
        </Alert>
      </Slide>
      <Container maxWidth="md" className={classes.mencont} disableGutters>
        <Typography className={classes.formhead}>
          Create a new Mentor/Admin :
        </Typography>
        <Grid container spacing={3}>
          <Grid item xs={12} lg={12}>
            <Paper className={classes.rpaper}>
              <Typography className={classes.userhead}>User Logo :</Typography>
              <PutInputs
                setPic={onDrop}
                inputs={[
                  {
                    id: 17,
                    name: "uplogo",
                  },
                ]}
                btn="none"
                func={createuser}
              />
            </Paper>
          </Grid>
          <Grid item xs={12} lg={12}>
            <Paper className={classes.rpaper}>
              <Typography className={classes.userhead}>
                User information :
              </Typography>
              <PutInputs
                FirstName={FirstName}
                LastName={LastName}
                email={email}
                password={password}
                setPhone={setPhone}
                setPic={onDrop}
                setValue={setValue}
                role={role}
                Phone={Phone}
                inputs={addmentor}
                ftype="cos"
                btnname="Add mentor"
                btn="none"
                form="none"
              />
            </Paper>
          </Grid>
          <Grid
            container
            item
            xs={12}
            alignItems="center"
            justifyContent="flex-end"
          >
            <Button
              variant="contained"
              color="secondary"
              style={{
                borderColor: "rgb(76, 175, 80)",
                color: "white",
                backgroundColor: "rgb(76, 175, 80)",
              }}
              onClick={() => {
                createuser();
              }}
            >
              Add member
            </Button>
          </Grid>
        </Grid>
      
      </Container>
    </>
  );
};

export const DeleteMentor = () => {
  return <Paper>Delete</Paper>;
};
