import { Paper, Typography } from "@material-ui/core";
import { useState } from "react";
import { DashOverView } from "./DashOverView";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import NotFound from "../../component/NotFound.js";
import { Getstartupcard } from "./StartupCard";
import Correction from "./Correction";
import Review from "./Review";
import { AddMentor, DeleteMentor } from "./Mentor";
import { Switch, Route, Redirect } from "react-router-dom";
import { useStyles } from "../../styling/dashbordstyle";
import Alert from "@material-ui/lab/Alert";
import Slide from "@material-ui/core/Slide";
// import Snackbar from '@material-ui/core/Snackbar';

export const InnerBoard = ({
  startup,
  startups,
  setStartups,
  token,
  setStartup,
  data,
  dashloading,
}) => {
  const [Salert, setSAlert] = useState({ state: false, msg: "" });
  const [Walert, setWAlert] = useState({ state: false, msg: "" });

  const classes = useStyles();
  return token ? (
    // <Container maxWidth="xl" className={classes.container}>
    <>
      <Switch>
        <Route exact path="/account/">
          <Container maxWidth="xl" className={classes.container}>
            {/* <Typography className={classes.Welcome}>
              Hi {data.firstname} Welcome Back !
            </Typography> */}
            <DashOverView
              startup={startup}
              data={data}
              dashloading={dashloading}
            />
          </Container>
        </Route>
        <Route path="/account/review">
          <Container
            maxWidth="xl"
            disableGutters
            className={classes.corcontainer}
          >
            <Paper className={classes.spaper}>
              <Review
                data={startups}
                setStartup={setStartup}
                token={token}
                setSAlert={setSAlert}
                setWAlert={setWAlert}
                udata={data}
              />
            </Paper>
          </Container>
        </Route>
        <Route path="/account/correction">
          <Container
            maxWidth="xl"
            disableGutters
            className={classes.corcontainer}
          >
            <Paper className={classes.spaper}>
              <Correction
                data={startups}
                setStartups={setStartups}
                token={token}
                setSAlert={setSAlert}
                udata={data}
                setWAlert={setWAlert}
              />
            </Paper>
          </Container>
        </Route>
        <Route path="/account/startups">
          <Container maxWidth="lg" className={classes.container}>
            <Slide
              direction="down"
              in={Salert.state}
              mountOnEnter
              unmountOnExit
            >
              <Alert
                severity="success"
                variant="filled"
                style={{ marginBottom: "2rem", zIndex: 99 }}
              >
                {Salert.msg}
              </Alert>
            </Slide>
            <Slide
              direction="down"
              in={Walert.state}
              mountOnEnter
              unmountOnExit
            >
              <Alert
                severity="warning"
                variant="filled"
                style={{ marginBottom: "2rem", zIndex: 99 }}
              >
                {Walert.msg}
              </Alert>
            </Slide>
            <Grid container spacing={3} className={classes.getcard}>
              <Getstartupcard
                startup={startup}
                token={token}
                setStartups={setStartups}
                setStartup={setStartup}
                data={data}
              />
            </Grid>
          </Container>
        </Route>
        <Route path="/account/addmember">
          <Container maxWidth="xl" className={classes.container}>
            {/* <Grid container spacing={3} className={classes.getcard}> */}
            <AddMentor
              // startup={startup}
              token={token}
              // setStartups={setStartups}
              // setStartup={setStartup}
              data={data}
            />
            {/* </Gri:d> */}
          </Container>
        </Route>
        <Route path="/account/deletemember">
          <Container maxWidth="xl" className={classes.container}>
            {/* <Grid container spacing={3} className={classes.getcard}> */}
            <DeleteMentor
              // startup={startup}
              token={token}
              // setStartups={setStartups}
              // setStartup={setStartup}
              data={data}
            />
            {/* </Grid:> */}
          </Container>
        </Route>
        <Route component={NotFound} />
      </Switch>
    </>
  ) : (
    // </Container>
    <Redirect to="/signin" />
  );
};
