// import { PieChart, Pie, Sector, Cell } from "recharts";
import { Container, Grid, Typography } from "@material-ui/core";
import Chart from "react-apexcharts";

function createData(time, amount) {
  return { time, amount };
}

const ldata = [
  createData("Jan", 0),
  createData("Feb", 10),
  createData("Mar", 20),
  createData("Apr", 30),
];

export default function Charts() {
  const theme = useTheme();
  const state = {
    options: {
      chart: {
        zoom: {
          enabled: false,
        },
      },
      title: {
        text: "Startups by Years",
        align: "left",
        margin: 10,
        offsetX: 0,
        offsetY: 0,
        floating: false,
        style: {
          fontSize: "14px",
          fontWeight: "bold",
          fontFamily: "Montserrat, sans-serif",
          color: "#263238",
        },
      },
      stroke: {
        curve: "smooth",
      },
      xaxis: {
        categories: [2020, 2021, 2022, 2023, 2024, 2025, 2026, 2027],
      },
    },
    series: [
      {
        name: "series-1",
        data: [30, 40, 45, 50, 49, 60, 70, 91],
      },
    ],
  };
  return (
    <>
      <Chart
        options={state.options}
        series={state.series}
        type="line"
        width="99%"
        height="250"
      />
    </>
  );
}

export const Piechart = ({ startup, role }) => {
  const data =
    role === "admin"
      ? [
          { name: "Recomanded", value: startup?.length },
          { name: "Strongly recomanded", value: startup?.length },
          { name: "Not recemanded", value: startup?.length },
          // { name: "Startup Total", value: startup.length  },
        ]
      : [
          { name: "onProgress", value: startup?.length },
          { name: "Evaluated", value: startup?.length },
          // { name: "Startup Total", value: startup.length  },
        ];
    const serie = data.map((elem) => (elem.value ? (elem.value) : (1)));
  const state = {
    options: {
      plotOptions: {
        pie: {
        donut: {
          size: "70%",
        },
      },
      },
      title: {
        text: "Startups Pie",
        align: "left",
        margin: 10,
        offsetX: 0,
        offsetY: 0,
        floating: false,
        style: {
          fontSize: "14px",
          fontWeight: "bold",
          fontFamily: "Montserrat, sans-serif",
          color: "#263238",
        },
      },
      legend: {
        position: "bottom",
        floating: false,
      },

      labels: data.map((elem) => elem.name),
    },
    series: serie,
    // chartOptions: {},
  };

  return (
    <Chart
      options={state.options}
      series={state.series}
      type="donut"
      // width="100%"
    />
  );
};
